.App {
  text-align: center;
  position: relative;
  height: 100vh;
  width: 100%;
}

.canvas {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
}

.logo-mask {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 512px;
  z-index: 2;
  opacity: .75;
}

.App-text-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-shadow: 0 0 10px rgba(0, 0, 0, 1);
  z-index: 3;
}

.App-title {
  font-size: 32px;
  font-family: 'Moul', serif;
}

.App-subtitle {
  font-size: 20px;
  font-family: 'Moul', serif;
}

@font-face {
  font-family: 'Moul';
  src: url('./Moul-Regular.ttf') format('truetype');
}